import React, { Component } from 'react';
import { Link } from "react-router-dom";
class Item extends Component {
    to_slug = (str) => {
        // Chuyá»n háº¿t sang chá»¯ thÆ°á»ng
        str = str.toLowerCase();

        // xÃ³a dáº¥u
        str = str.replace(/(Ã |Ã¡|áº¡|áº£|Ã£|Ã¢|áº§|áº¥|áº­|áº©|áº«|Ä|áº±|áº¯|áº·|áº³|áºµ)/g, 'a');
        str = str.replace(/(Ã¨|Ã©|áº¹|áº»|áº½|Ãª|á»|áº¿|á»|á»|á»)/g, 'e');
        str = str.replace(/(Ã¬|Ã­|á»|á»|Ä©)/g, 'i');
        str = str.replace(/(Ã²|Ã³|á»|á»|Ãµ|Ã´|á»|á»|á»|á»|á»|Æ¡|á»|á»|á»£|á»|á»¡)/g, 'o');
        str = str.replace(/(Ã¹|Ãº|á»¥|á»§|Å©|Æ°|á»«|á»©|á»±|á»­|á»¯)/g, 'u');
        str = str.replace(/(á»³|Ã½|á»µ|á»·|á»¹)/g, 'y');
        str = str.replace(/(Ä)/g, 'd');

        // XÃ³a kÃ½ tá»± Äáº·c biá»t
        str = str.replace(/([^0-9a-z-\s])/g, '');

        // XÃ³a khoáº£ng tráº¯ng thay báº±ng kÃ½ tá»± -
        str = str.replace(/(\s+)/g, '-');

        // xÃ³a pháº§n dá»± - á» Äáº§u
        str = str.replace(/^-+/g, '');

        // xÃ³a pháº§n dÆ° - á» cuá»i
        str = str.replace(/-+$/g, '');

        // return
        return str;
    }

    render() {
        return (
            <div className="item">
                <div className="animate-box">
                    <a href={"images/" + this.props.image} className="image-popup fh5co-board-img" title={this.props.name}><img src={"images/" + this.props.image} alt={this.props.name} /></a>
                </div>
                <Link to = {"/detail/" + this.props.pId + "/" + this.to_slug(this.props.name) + ".html"}><div className="fh5co-desc">{this.props.name}</div></Link>
            </div>
        );
    }
}

export default Item;